<template>
<div class="container">
    <section class="top">
        <div class="avator">
            <img src="@/assets/avator.jpg" alt="头像" />
        </div>
        <h1>陈旭钦</h1>
        <h2>Web前端开发工程师</h2>
    </section>
    <section class="info">
        <div>男 / 1997.02</div>
        <div>广东工业大学 本科</div>
        <div>居住地: 广州市</div>
        <div>当前状态: 2020年应届生求职</div>
    </section>
    <section class="myUrl">
        <ul>
            <li v-for="(item, index) in myUrl" :key="index">
